.header {
  padding: 20px 0 30px;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 70px;
    padding: 18px 30px;
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fff;
      border: 1px solid #F0F0F0;
      border-radius: 30px;
      pointer-events: none;
    }
  }
  &__left {
    position: relative;
    z-index: 3;
  }
  &__right {
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    gap: 150px;
  }
  &__logo {
    width: 151px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 70px;
    li a {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      &:hover {
        color: #0039C6;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__social {
    width: 54px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #0039C6;
    border-radius: 50%;
    &:hover {
      background: #333834;
    }
    svg {
      max-width: 32px;
      height: auto;
    }
  }
  &__btn {
    padding: 15px 50px;
    span {
      font-size: 20px;
    }
  }
}
.burger {
  position: relative;
  z-index: 3;
  width: 66px;
  height: 30px;
  display: none;
  span {
    position: absolute;
    right: 0;
    height: 5px;
    background: linear-gradient(26.96deg, rgb(0, 57, 198) 5.618%,rgb(65, 120, 255) 73.232%);
    border-radius: 5px;
  }
  span:nth-child(1) {
    top: 0;
    width: 100%;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
    width: 50px;
  }
  span:nth-child(3) {
    bottom: 0;
    width: 100%;
  }
}

@media screen and (max-width: 1600px) {
  .header {
    &__inner {
      gap: 30px;
    }
    &__right {
      gap: 20px;
    }
    &__list {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__inner {
      padding: 18px;
    }
    &__list {
      li a {
        font-size: 16px;
      }
    }
    &__btn {
      padding: 15px 30px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .header {
    padding: 10px 0 20px;
    &__right {
      position: fixed;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      flex-direction: column;
      justify-content: start;
      gap: 50px;
      background: #fff;
      padding: 140px 20px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      &:after {
        content: '';
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 80px;
        background: #fff;
        pointer-events: none;
      }
    }
    &__logo {
      width: 188px;
    }
    &__list {
      max-width: 440px;
      width: 100%;
      flex-direction: column;
      gap: 10px;
      li {
        width: 100%;
      }
      li a {
        width: 100%;
        height: 77px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        background: #AFED15;
        border: 1px solid #F0F0F0;
        border-radius: 35px;
        font-size: 24px;
        &:hover {
          background: #333834;
          color: #fff;
        }
      }
    }
    &__group {
      max-width: 440px;
      width: 100%;
      flex-direction: column;
      gap: 30px;
    }
    &__social {
      width: 82px;
      height: 82px;
      img {
        max-width: 45px;
        min-width: 36px;
        width: auto;
      }
    }
    &__btn {
      width: 100%;
      min-height: 77px;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 450px) {
  .header {
    &__logo {
      width: 151px;
    }
    &__list {
      li a {
        height: 60px;
        font-size: 20px;
      }
    }
    &__group {
      gap: 20px;
    }
    &__social {
      width: 54px;
      height: 54px;
      img {
        max-width: 32px;
        min-width: unset;
      }
    }
    &__btn {
      min-height: 60px;
    }
  }
}