.main {
  padding-bottom: 65px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 30px;
  }
  &__left {
    position: relative;
    width: calc(100% - 610px);
    background-image: url("../img/main-img2.webp");
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: cover;
    border-radius: 30px;
    padding: 60px 60px 125px;
    overflow: hidden;
  }
  &__right {
    width: 580px;
    .main__btn {
      display: none;
    }
  }
  &__title {
    font-family: 'DespairDisplay';
    font-size: 64px;
    line-height: 110%;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    span {
      background: #AFED15;
      border-radius: 30px;
      padding: 4px 8px;
      color: #0039C6;
    }
  }
  &__text {
    max-width: 460px;
    margin-top: 30px;
    p {
      font-size: 20px;
      line-height: 100%;
      color: #Fff;
    }
  }
  &__btn {
    position: absolute;
    left: 0;
    bottom: 0;
    gap: 12px;
    padding: 22px 32px;
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
    span {
      font-size: 20px;
    }
    i {
      max-width: 32px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  .figure {
    position: absolute;
    right: 140px;
    bottom: -250px;
    width: 187px;
    pointer-events: none;
    transition: all 0.8s;
    img {
      width: 100%;
    }
  }
  .figure2 {
    position: absolute;
    right: 36px;
    bottom: 146px;
    width: 398px;
    transition: all 0.8s;
    img {
      width: 100%;
    }
  }
  .figure.active {
    right: 40px;
    bottom: -154px;
    transform: rotate(-90deg);
  }
  .figure2.active {
    right: 220px;
    bottom: -50px;
    transform: rotate(-90deg);
  }
  &__img {
    position: relative;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__info {
    position: absolute;
    z-index: 1;
    left: 30px;
    right: 30px;
    bottom: 30px;
  }
  &__name {
    font-size: 26px;
    line-height: 120%;
    font-weight: 600;
  }
  &__post {
    font-size: 16px;
    line-height: 120%;
    color: #828282;
    margin-top: 5px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    border: 1px solid #F0F0F0;
    border-radius: 30px;
    padding: 30px 40px 40px;
    &:nth-child(odd) {
      background: #F0F0F0;
    }
    &:nth-child(even) {
      background: #fff;
    }
    &:nth-child(2) {
      transition: all 0.9s;
    }
    &:hover {
      background: linear-gradient(90.00deg, rgb(0, 57, 198) -2.5%,rgb(65, 120, 255) 100%);
      .main__item-icon {
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
      .main__item-text {
        p {
          color: #fff;
        }
      }
    }
    &-icon {
      max-width: 65px;
      height: 60px;
      img {
        max-width: 65px;
        height: auto;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    &-text {
      margin-top: 50px;
      p {
        font-size: 20px;
        line-height: 120%;
        font-weight: 500;
        transition: all 0.3s;
      }
    }
  }
}
.info {
  padding: 65px 0;
  .section-top {
    max-width: 806px;
    margin: 0 auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
