    }
  }
  &__line {
    position: absolute;
    z-index: 0;
    right: 0;
    bottom: 0;
    max-width: 383px;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
  }
  &__img2 {
    position: relative;
    img {
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    background: #fff;
    border: 1px solid #F0F0F0;
    border-radius: 30px;
    padding: 30px;
    margin-top: 30px;
    li {
      display: flex;
      gap: 18px;
      font-size: 16px;
      line-height: 120%;
      span {
        width: 21px;
        flex-shrink: 0;
