        }
      }
    }
  }
  &__info {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    span {
      font-family: 'DespairDisplay';
      font-size: 32px;
      line-height: 138%;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
}
