.service-card {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  &:nth-child(odd) {
    background: linear-gradient(90.00deg, rgb(0, 57, 198) -2.5%,rgb(65, 120, 255) 100%);
    &:after {
      background: #fff;
    }
    .service-card__title {
      color: #fff;
      padding: 40px 16px;
    }
    .service-card__list {
      li {
        span {
          background: linear-gradient(90.00deg, rgb(0, 57, 198) -2.5%,rgb(65, 120, 255) 100%);
        }
      }
    }
  }
  &:nth-child(even) {
    background: #fff;
    transition: all 0.9s;
    &:after {
      background: linear-gradient(90.00deg, rgb(0, 57, 198) -2.5%,rgb(65, 120, 255) 100%);
    }
    .service-card__title {
      color: #0039C6;
      padding: 40px 16px;
    }
    .service-card__list {
      li {
        color: #fff;
        span {
          background: #AFED15;
        }
      }
    }
    .service-card__price {
      color: #fff;
      span {
        color: #AFED15;
      }
    }
  }
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% - 120px);
    border-radius: 30px;
    pointer-events: none;
  }
  &__title {
    text-align: center;
    border-radius: 30px 30px 0 0;
  }
  &__list {
    position: relative;
    z-index: 2;
    max-width: 383px;
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    padding: 60px 16px;
    margin: 0 auto;
    li {
      display: flex;
      gap: 18px;
      font-size: 16px;
      line-height: 120%;
      span {
        width: 45px;
        height: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        flex-shrink: 0;
        margin-top: -14px;
        img {
          max-width: 100%;
        }
      }
      strong {
        display: contents;
        font-weight: 500;
        color: #0039C6;
        margin-left: -12px;
      }
    }
  }
  &__bottom {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0 16px 50px;
    margin-top: auto;
  }
  &__price {
    font-size: 20px;
    line-height: 120%;
    span {
      font-family: 'DespairDisplay';
      font-size: 32px;
      font-weight: 700;
      color: #0039C6;
    }
  }
  &__btn {
    padding: 20px 25px;
    margin-top: 20px;
    span {
      font-size: 20px;
    }
  }
  &__line {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: 0;
    pointer-events: none;
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .tag {
    position: absolute;
    z-index: 2;
    top: -16px;
    left: -24px;
    transform: rotate(-8deg);
    padding: 13px 24px;
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
  }
}
