.wrapper {
  overflow: hidden;
}
.btn-black {
  background: #333834;
  border-radius: 30px;
  &:hover {
    background: #0039C6;
  }
  span {
    line-height: 120%;
    color: #fff;
    font-weight: 500;
  }
}
.btn-green {
  background: #AFED15;
  border-radius: 30px;
  &:hover {
    background: #333834;
    span {
      color: #fff;
    }
    i svg path {
      fill: #fff;
    }
  }
  span{
    line-height: 120%;
    font-weight: 500;
    color: #333834;
  }
}
.btn-blue {
  background: #0039C6;
  border-radius: 30px;
  &:hover {
    background: #333834;
  }
  span{
    line-height: 120%;
    font-weight: 500;
    color: #fff;
  }
}
.bottom-anim {
  transform: translate(0, 60%);
  opacity: 0;
  transition: all 0.8s;
}
.bottom-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.8s;
}
.right-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.8s;
}
.left-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.section-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  h2 {
    margin-top: 40px;
    span {
      display: inline-block;
      background: rgb(0, 57, 198);
      border-radius: 30px;
      padding: 2px 5px;
      color: #fff;
    }
  }
}
.text32 {
  font-size: 32px;
  line-height: 130%;
  font-weight: 500;
}
.text20 {
  font-size: 20px;
  line-height: 120%;
  color: #0039C6;
}
.swiper-button-prev,
.swiper-button-next {
  width: 61px;
  height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #AFED15;
  border-radius: 50%;
  transition: all 0.3s;
  &:after {
    display: none;
  }
  &:hover {
    background: #333834;
    svg path {
      stroke: #fff;
    }
  }
  svg {
    width: 32px;
    height: auto;
    path {
      transition: all 0.3s;
    }
  }
}
.form {
  &-group {
    width: 100%;
    input {
      width: 100%;
      height: 70px;
      background: #fff;
      border: none;
      border-radius: 30px;
      padding: 0 30px;
      font-size: 20px;
      line-height: 120%;
      color: #333834;
      &::placeholder {
        color: #828282;
      }
    }
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #0039C6;
        border-color: #0039C6;
      }
    }
    label {
      position: relative;
      font-size: 16px;
      line-height: 140%;
      color: #828282;
      padding-left: 44px;
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80%;
        border: 2px solid #828282;
        border-radius: 5px;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #0039C6;
        }
      }
    }
  }
  &-btn {
    padding: 20px 25px;
    span {
      font-size: 20px;
    }
  }
}
.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 670px;
    max-width: 100%;
    background: #F0F0F0;
    border-radius: 30px;
    padding: 88px 60px 80px;
    margin: 0 auto;
    overflow: hidden;
    @media screen and (max-width: 992px) {
      max-width: calc(100% - 30px);
    }
  }
  .form {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 30px;
    &-btn__wrap {
      display: flex;
      justify-content: center;
    }
    &-btn {
      min-width: 377px;
      border-top: 20px;
    }
  }
  &__title {
    text-align: center;
    font-family: 'DespairDisplay';
    font-weight: 700;
    color: #0039C6;
    text-transform: uppercase;
  }
  &__close {
    position: absolute;
    top: 32px;
    right: 32px;
    background: transparent;
    padding: 0;
    width: 38px;
    img {
      width: 100%;
    }
  }
  &__line {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px) {
  .section-top {
    h2 {
      margin-top: 30px;
    }
  }
  .text32 {
    font-size: 28px;
  }
}

@media screen and (max-width: 576px) {
  .modal {
    &__dialog {
      padding: 88px 20px 40px;
    }
    .form {
      &-btn {
        width: 100%;
        min-width: unset;
      }
    }
  }
}

@media screen and (max-width: 460px) {
  .form {
    &-group {
      input {
        font-size: 16px;
      }
    }
    &-btn {
      span {
        font-size: 16px;
      }
    }
  }
}