.footer {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 40px;
    &:nth-child(2) {
      gap: 10px;
    }
    p {
      font-size: 14px;
      line-height: 130%;
      color: #828282;
    }
    p a:hover {
      color: #0039C6;
    }
  }
  .brevis {
    width: 111px;
    display: block;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      flex-direction: column;
      gap: 20px;
    }
    &__group {
      flex-wrap: wrap;
      justify-content: center;
      gap: 20px;
    }
  }
}